@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

/* Container styles */

.container{
  width: 80%;
  margin-left: auto; 
  margin-right: auto;  
}

@media (max-width: 900px) {
  .container{
      width: 100%;  
  }
}

/* Header styles */

.header{    
    color: #fff;
    background-color: #55efc4;
    padding: 80px 150px;
}

.header__title{
    font-weight: 400;
    font-size: 30px;
    margin-bottom: 20px;
}

.header__text{
    font-weight: 300;
    font-size: 20px;
}

@media (max-width: 900px) {
    .header{
        padding: 80px 50px;  
    }
}
/* filter */

.filter{
    background-color: #0984e3;
    display: flex;
    justify-content: space-evenly;
    padding: 15px 30px;
}

.filter__container{
    background-color: #fff;
    padding: 3px;
    border-radius: 5px;
    margin: 0 10px;
}

.filter__input, .filter__select{
    border: none;
    outline: none;
    width: 130px;
    height: 25px;
}

.filter__input-icon{
    color: #b2bec3;
    margin: 0 3px;
}

.filter__button{
    background-color: #d63031;
    border: none;
    border-radius: 5px;
    color: white;
    padding: 10px 15px;
    text-decoration: none; 
    width: 120px; 
    cursor: pointer;    
}

.filter__button-icon{
    margin: 0 2px;
}

@media (max-width: 900px) {
    .filter{
        justify-content: flex-start;
        flex-wrap: wrap; 
        padding: 20px 20px;     
    }

    .filter__container{
        width: 45%;
        margin: 10px 10px;
    }

    .filter__input, .filter__select{
        width: 90%;
    }

    .filter__button{
        margin-left: 10px;
    }
}

@media (max-width: 668px) {
    .filter{
        flex-direction: column;
        flex-wrap: wrap; 
        padding: 10px 10px;     
    }

    .filter__container{
        width: 100%;
        margin: 10px 0;
    }

    .filter__input, .filter__select{
        width: 90%;
    }

    .filter__button{
        margin-left: 0px;
        height: 40px;
        width: 100%;
    }
}
/* Cards */

.cards{
    padding: 20px 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}
.card{
    width: 32%;
    border: 1px solid #dfe6e9;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 20px;
    margin-right: 10px;
}

.card__image{
    width: 100%;
    margin-bottom: 20px;
}

.card__title{
    font-weight: 400;
    font-size: 20px;
    margin-bottom: 10px;
}

.card__date{
    background-color: #00b894;
    color: #fff;
    padding: 5px;
    margin: 5px 0;
    font-size: 15px;
}

.card__description{
    color: #b2bec3;
    height: 80px;
    overflow-y: scroll;
    margin: 10px 0;
    font-size: 14px;
}

.card__description::-webkit-scrollbar{
    width: 5px;
    height: 5px;
}

.card__description::-webkit-scrollbar-thumb {
    background: #00b894;
    border-radius: 4px;
}

.card__description::-webkit-scrollbar-track {
    background: #e1e1e1;
    border-radius: 4px;
}

.card__info{
    display: flex;
    align-items: center;
    margin: 10px 0;
    border-radius: 5px;    
    box-shadow: 2px 2px 2px #dfe6e9; 
    background-color: #fcfcfc;
    font-size: 14px;
}

.card__info-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.card__info-icon{
    background-color: #0984e3;
    padding: 10px ; 
    border-radius: 5px; 
    color: #fff;     
}

.card__info-text{
    padding-left: 10px;
}

.card__info--medium{
    width: 60%;
}

.card__button{
    background-color: #00b894;
    border: none;
    border-radius: 5px;
    color: white;
    padding: 10px 15px;
    text-decoration: none;
    width: 100%;
    font-size: 18px;
    cursor: pointer;
    margin-top: 10px;
}

@media (max-width: 900px) {
    .card{
        width: 49%;
    }

}

@media (max-width: 668px) {
    .card{
        width: 100%;
    }

}

.card__counter{
    background-color: #0984e3;
    padding: 10px; 
    border-radius: 5px; 
    color: #d2d2d2;  
}

.card__counter-icon{
    margin: 0 3px;
}

.active{
    color: #fff;
}
